  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/**
 * Add margin 0 to blockquote
*/
blockquote {
  margin: 0;
}

.contenido-centrado {
  max-width: 800px;
}

.dark {
  background-color: #262626;
}
.dark * {
  color: #e1e1e1;
}
.dark p {
  color: #FFFFFF;
}
.dark .anuncio {
  background-color: #454545;
  border: 1px solid #333333;
}
.dark .anuncio__content--precio {
  color: #71B100;
}
.dark .anuncio__content--icons img {
  filter: invert(100%);
}
.dark .content__article--entrada h4 {
  color: #f3f3f3;
}
.dark .blog .content__article {
  background-color: #404040;
}
.dark .blog .content__article:hover {
  background-color: #4d4d4d;
}
.dark .form__section .input input, .dark .form__section .input textarea, .dark .form__section .input select {
  background-color: #3d3d3d;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
}
.footer__box {
  padding-top: 2.5rem;
}
.footer .copyrigth {
  color: #FFFFFF;
  text-align: center;
  margin-top: 3rem;
}

.footer__box {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__box-nav {
  display: flex;
  gap: 1rem;
}
@media (min-width: 768px) {
  .footer__box-nav {
    display: flex;
  }
}
.footer__box-nav a {
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  display: block;
  position: relative;
  z-index: 0;
  border-bottom: 2px solid transparent;
}
.footer__box-nav a:hover {
  color: #71B100;
  border-color: #71B100;
  transition: all 0.3s ease;
}
@media (min-width: 768px) {
  .footer__box-nav a {
    margin-right: 2rem;
    font-size: 2.5rem;
  }
  .footer__box-nav a:last-of-type {
    margin-right: 0;
  }
}

.no-webp .home {
  background-image: url("../img/header.jpg");
}

.webp .home {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.home {
  background-position: center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.home {
    min-height: 70rem;
  }
}
.header.home .header__box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header.home .header__box {
    min-height: 70rem;
  }
}
.header.home .header__box h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: 300;
  font-size: 2rem;
  max-width: 30rem;
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .header.home .header__box h1 {
    margin-top: 0;
    font-size: 4rem;
    max-width: 60rem;
  }
}

.header__box--bar {
  display: flex;
  padding-top: 3rem;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__box--bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.header__box--bar .bar__logo {
  min-width: 25rem;
  width: 40%;
  color: #FFFFFF;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__box--bar .bar__logo {
    width: auto;
  }
}
.header__box--bar .bar__nav {
  display: flex;
  gap: 2rem;
  align-items: center;
}

.bar__logo h1 {
  font-weight: 300;
}
.bar__logo h1 span {
  font-weight: 700;
}

.bar__nav a {
  display: none;
  color: #FFFFFF;
  font-size: 2.2rem;
  transition: color 0.3s;
}
@media (min-width: 768px) {
  .bar__nav a {
    display: block;
  }
}
@media (min-width: 768px) {
  .bar__nav a {
    margin-right: 2rem;
  }
  .bar__nav a:last-of-type {
    margin-right: 0;
  }
}
.bar__nav a:hover {
  color: #71B100;
}
.bar__nav--responsive {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10rem;
  transition: background-color 0.3s ease;
  background-color: #333333;
}
.bar__nav--responsive a {
  transition: color 0.3s ease;
  color: transparent;
  color: #FFFFFF;
  font-size: 4rem;
  margin: 0;
}
.bar__nav--responsive .theme__button {
  display: none;
}
.bar__nav .theme__button,
.bar__nav .responsive__button, .bar__nav--responsive .responsive__button {
  cursor: pointer;
  line-height: 0;
}
.bar__nav .theme__button .checkbox,
.bar__nav .responsive__button .checkbox, .bar__nav--responsive .responsive__button .checkbox {
  display: none;
}
.bar__nav .theme__button {
  color: #FFFFFF;
  font-size: 3.5rem;
}
@media (min-width: 768px) {
  .bar__nav .theme__button {
    padding: 0 2rem;
  }
}
.bar__nav .responsive__button, .bar__nav--responsive .responsive__button {
  font-size: 3.5rem;
  color: #FFFFFF;
  padding-right: 1rem;
  margin: 0;
}
@media (min-width: 768px) {
  .bar__nav .responsive__button, .bar__nav--responsive .responsive__button {
    display: none;
  }
}
.bar__nav--responsive .responsive__button {
  position: absolute;
  top: 4.3rem;
  right: 1.4rem;
}

@media (min-width: 768px) {
  .about__content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.about__content--text blockquote {
  font-weight: 700;
  font-size: 2.2rem;
  margin: 0;
  padding: 1rem 0 3rem 0;
}

.propiedad {
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
}
.propiedad__title {
  margin-bottom: 2rem;
}
.propiedad__resumen {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

.propiedad__resumen--precio {
  font-size: 3rem;
  font-weight: 700;
  color: #E08709;
}
.propiedad__resumen--icons {
  width: 30rem;
}

.blog {
  gap: 0rem;
}
.blog .content__article {
  background-color: #e1e1e1;
  padding: 3rem;
  --br: 2rem;
}
.blog .content__article, .blog .content__article--img {
  border-radius: var(--br);
}

.blog .content__article {
  --t: all 0.3s ease;
  transition: var(--t);
}
.blog .content__article--img {
  overflow: hidden;
}
.blog .content__article:hover {
  background-color: #bbbbbb;
}
.blog .content__article:hover .content__article--img img {
  transition: var(--t);
  transform: scale(1.1);
}

.entrada {
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
}
.entrada__title {
  margin-bottom: 2rem;
}
.entrada__content {
  margin: 2rem 0;
  padding: 2rem;
}
.entrada__content p {
  font-weight: 700;
  line-height: 2.5;
  text-align: justify;
}
.entrada__meta {
  margin-bottom: 1rem;
  font-weight: 700;
  font-size: 1.7rem;
}
.entrada__meta span {
  color: #E08709;
}

.contact__form {
  padding: 0 1rem;
}
.contact__form--form {
  max-width: 100rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form__section {
  transition: all 0.3s ease;
  padding: 1rem 3rem;
  border: 0.3rem solid #000000;
}
.form__section legend {
  padding: 0 1rem;
  font-size: 1.8rem;
  font-weight: 700;
}
.form__section p {
  font-size: 1.4rem;
  font-weight: 400;
  margin: 1rem 0;
}
.form__section .input {
  display: block;
  margin-bottom: 1rem;
  box-sizing: border-box;
}
.form__section .input__label {
  display: block;
  padding: 0rem 1.5rem;
  font-size: 1.5rem;
  font-weight: 700;
}
.form__section .input input, .form__section .input textarea, .form__section .input select {
  transition: all 0.3s ease;
  background-color: #e1e1e1;
  font-weight: 400;
  width: 100%;
  padding: 0.8rem;
  border: none;
  outline: none;
}
.form__section .input select {
  width: auto;
}
.form__section .input textarea {
  width: 100%;
  height: 20rem;
  resize: none;
}
.form__section .checkbox .input__label,
.form__section .checkbox input[type=checkbox] {
  display: inline;
  width: auto;
}

.submit {
  text-align: right;
}
.submit .button {
  margin-top: 1rem;
  background-color: #71B100;
  color: #FFFFFF;
}
.submit .button:hover {
  background-color: #E08709;
}

.section__anuncios--title {
  margin-bottom: 3rem;
}
.section__anuncios--content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .section__anuncios--content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  display: flex;
  flex-direction: column;
  background-color: #e1e1e1;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  overflow: hidden;
}
.anuncio__img {
  box-sizing: border-box;
  overflow: hidden;
}
.anuncio__img:hover img {
  transition: transform 0.3s ease;
  transform: scale(1.15);
}
.anuncio__content {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.anuncio__content {
  display: flex;
  flex-direction: column;
  gap: 1.1rem;
}
.anuncio__content--precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}
.anuncio__content--icons {
  display: flex;
  padding: 0;
  margin-bottom: 3rem;
}
.anuncio__content--icons li {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.anuncio__content--icons li img {
  flex-basis: 3rem;
}
.anuncio__content--icons li p {
  font-weight: 700;
}
.anuncio__content .button {
  background-color: #E08709;
  color: #FFFFFF;
  display: block;
  text-align: center;
}
.anuncio__content .button:hover {
  background-color: #71B100;
}

.view-all {
  display: flex;
  margin-bottom: 2rem;
  justify-content: flex-end;
}
.view-all .button {
  background-color: #71B100;
  color: #FFFFFF;
}
.view-all .button:hover {
  background-color: #E08709;
}

.contact {
  background-color: #333333;
  background-image: url("../img/encuentra.jpg");
  background-position: center;
  background-size: cover;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 0;
}
.contact h2, .contact p {
  max-width: 90%;
}
.contact h2 {
  font-weight: 700;
}
.contact p {
  color: #FFFFFF;
  text-align: center;
  font-weight: 300;
}
.contact .button {
  background-color: #E08709;
  color: #FFFFFF;
  margin-top: 2rem;
}
.contact .button:hover {
  background-color: #71B100;
}

.about__title {
  margin-bottom: 3rem;
}
.about__icons {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .about__icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.about__icons .icon {
  text-align: center;
  border-bottom: 0.1rem solid #333333;
  margin-top: 4rem;
}
.about__icons .icon:first-of-type {
  margin-top: 0;
}
@media (min-width: 768px) {
  .about__icons .icon {
    margin: 0;
    border-bottom: none;
  }
}
.about__icons .icon img {
  height: 10rem;
  width: auto;
  margin: 0 auto;
}
.about__icons .icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .section__blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
.section__blog--content {
  display: flex;
  flex-direction: column;
}
.section__blog--content .content__article {
  margin-bottom: 2rem;
}
.section__blog--content .content__article:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .section__blog--content .content__article {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
    gap: 2rem;
    box-sizing: border-box;
  }
}

.content__article--img {
  height: 100%;
}
.content__article--entrada {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.content__article--entrada h4, .content__article--entrada p {
  text-align: left;
}
.content__article--entrada h4 {
  margin-top: 1.3rem;
  line-height: 1.4;
  text-align: left;
  color: #000000;
  font-weight: 700;
}
@media (min-width: 768px) {
  .content__article--entrada h4 {
    margin-top: 0;
  }
}
.content__article--entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.content__article--entrada p {
  font-weight: 400;
}
.content__article--entrada span {
  color: #E08709;
}
.content__article--entrada .view-entrada {
  display: flex;
  justify-content: flex-end;
}
.content__article--entrada .view-entrada .button {
  background-color: #E08709;
  color: #FFFFFF;
  display: inline;
  width: fit-content;
}
.content__article--entrada .view-entrada .button:hover {
  background-color: #71B100;
}

.section__blog--testimoniales .testimoniales__content {
  box-sizing: border-box;
}
.section__blog--testimoniales .testimoniales__content .testimonial {
  background-color: #71B100;
  width: 100%;
  height: 100%;
  padding: 3rem;
  border-radius: 2rem;
}
.section__blog--testimoniales .testimoniales__content .testimonial blockquote, .section__blog--testimoniales .testimoniales__content .testimonial p {
  color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 700;
}
.section__blog--testimoniales .testimoniales__content .testimonial blockquote {
  --pl: 6rem;
  position: relative;
  padding-left: var(--pl);
}
.section__blog--testimoniales .testimoniales__content .testimonial blockquote::after {
  position: absolute;
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: var(--pl);
  height: var(--pl);
  display: block;
  top: 0;
  left: 0;
}
.section__blog--testimoniales .testimoniales__content .testimonial p {
  text-align: right;
}

.content__title,
.testimoniales__title {
  margin-bottom: 1rem;
}

@media (min-width: 768px) {
  .about__content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.about__content--text blockquote {
  font-weight: 700;
  font-size: 2.2rem;
  margin: 0;
  padding: 1rem 0 3rem 0;
}

.admin .button {
  background-color: #71B100;
  color: #FFFFFF;
}
.admin .button:hover {
  background-color: #E08709;
}

.create .alert {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.create .alert .err, .create .alert .enviado {
  padding: 0.7rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: bold;
  width: 100%;
  text-align: center;
}
.create .alert .err {
  background-color: #ff2d2d;
}
.create .alert .enviado {
  background-color: #71B100;
}
.create .button {
  background-color: #71B100;
  color: #FFFFFF;
}
.create .button:hover {
  background-color: #E08709;
}

.back {
  font-size: 2.5rem;
  color: #71B100;
}
.back i {
  transform: rotate(180deg);
}

.DB__form .input {
  --border: .3rem solid;
}
.DB__form .input input, .DB__form .input textarea {
  border: var(--border) transparent;
}
.DB__form .input .validate {
  border: var(--border) #71B100;
}
.DB__form .submit .button {
  border: none;
  cursor: pointer;
}